@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        // grid-template-columns: repeat(2, 1fr);
        grid-template-columns: 1fr 55%;
        min-height: 100vh;
    }

    .imagen {
        height: 25rem;
        background-image: url(../img/1.jpg);
        background-size: cover;
        background-position: center center;
        @include m.tablet {
            height: auto;
        }

    }
    
    .navBar {
        background-color:v.$azul;
        overflow: hidden;
        font-weight: bold;
        a {
            float: left;
            color: v.$blanco;
            text-align: center;
            padding: 1rem 1.5rem;
            text-decoration: none;
            font-size: 1.9rem;
            &:hover {
                background-color: v.$blanco;
                color: v.$azul;
              }
              &.active {
                background-color: v.$blanco;
                color: v.$azul;
              }
              .fa-house{
                font-size: 2rem;
                
              }
          }
      }

    .app {
        width: 100%;
        margin: 0 auto;
        padding: 3rem 2rem;
        @include m.tablet {
            overflow: scroll;
        }
    }
}

.barra {
    margin: v.$separacion 0;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items:center;
    }
    span{
        color: v.$azul;
    }
    .boton {
        @include m.boton(v.$negro, v.$blanco);
        margin: 0;
        transition: color 300ms ease;
        &:hover{
            background-color: v.$negro;
            color: v.$azul;
        }
    }
}

.barra-servicios {
    margin-bottom: v.$separacion;

    @include m.tablet {
        display: flex;
        justify-content: space-around;
    }

    .boton {
        margin: 0 0 1rem 0;
    }
}